import React from "react";
import clsx from "clsx";
import style from "./main.module.scss";
import { ReactComponent as Arrow } from "./arrow.svg";
import { Dropdown } from "antd";

export const LINKS = [
  {
    name: "Twitter",
    href: "https://twitter.com/NekoSwap",
    src: require("./x.svg").default,
  },
  {
    name: "Discord",
    href: "https://discord.gg/nekoswap",
    src: require("./discord.svg").default,
  },
];

const items = LINKS.map((item, index) => {
  return {
    key: index,
    label: (
      <a target="_blank" key={index} rel="noopener noreferrer" href={item.href}>
        <div className="flex flex--center">
          <img style={{width: "16px", marginRight: "5px"}} src={item.src} alt={item.name} />
          {item.name}
        </div>
      </a>
    ),
  };
});

const DropList = function () {
  return (
    <Dropdown
      menu={{
        items,
      }}
      placement="bottomLeft"
    >
      <div className={clsx(["flex flex--center", style.drop])}>
        <h3 className={style.drop_text}>socials</h3>
        <Arrow />
      </div>
    </Dropdown>
  );
};

export default DropList;
