import React from "react";
import clsx from "clsx";
import style from "./main.module.scss";
const TEXTS = ["$Rneko", "To", "The", "Moon"];
export const MARQUEES = [
  {
    suffix: {
      src: require("../../../images/marquee_1.png"),
      width: "50px",
    },
    color: "#FFF3A3",
  },
  {
    suffix: {
      src: require("../../../images/marquee_2.png"),
      width: "49px",
    },
    color: "#5D95D2",
  },
  {
    suffix: {
      src: require("../../../images/marquee_3.png"),
      width: "84px",
    },
    color: "#78C9B6",
  },
  {
    suffix: {
      src: require("../../../images/marquee_4.png"),
      width: "35px",
    },
    color: "#B5DAF1",
  },
];

const MarqueeItem = function (props) {
  const suffix = props.pink.suffix;
  return (
    <div className={clsx([style.marquee_item, "flex flex--center"])}>
      <div
        className={style.marquee_item__content}
        style={{ background: props.pink.color }}
      >
        {props.children}
      </div>
      {suffix && (
        <div
          className={style.marquee_item__suffix}
          style={{ width: suffix.width }}
        >
          <img src={suffix.src} alt={props.name} />
        </div>
      )}
    </div>
  );
};

const Marquee = function () {
  const duration = TEXTS.length * 5.5 + "s";
  const arr = new Array(3).fill(1);
  return (
    <div className={style.marquee_container}>
      {arr.map((_, index) => {
        return (
          <ul
            key={index}
            className={clsx(["flex marquee-animate", style.marquee])}
            style={{ "--marquee-duration": duration }}
          >
            {TEXTS.map((item, index) => {
              const count = index % MARQUEES.length;
              const pink = MARQUEES[count] || {};
              return (
                <li key={index} className={style.marquee_gap}>
                  <MarqueeItem pink={pink}>{item}</MarqueeItem>
                </li>
              );
            })}
          </ul>
        );
      })}
    </div>
  );
};

export default Marquee;
