import React from "react";
import style from "./main.module.scss";

const imgs = [
  require("../../images/bg_1.png"),
  require("../../images/bg_2.png"),
  require("../../images/bg_3.png"),
  require("../../images/bg_4.png"),
  require("../../images/bg_5.png"),
  require("../../images/bg_6.png"),
  require("../../images/bg_7.png"),
  require("../../images/bg_8.png"),
  require("../../images/bg_9.png"),
];

const Wrapper = function ({ children }) {
  return (
    <div className={style.app}>
      <div className={style.content}>{children}</div>

      {imgs.map((url, index) => {
        return (
          <img
            className={style[`img_star_${index + 1}`]}
            src={url}
            alt={index}
            key={index}
          />
        );
      })}
    </div>
  );
}

export default Wrapper;